/** Media Queries **/
.restablecer {
  margin-top: 5rem;
}
.restablecer .uptask {
  background: linear-gradient(to right, #4338CA 0%, #b5b1ea 100%);
  color: transparent;
  background-clip: text;
  font-size: 7rem;
  text-align: center;
}
@media (min-width: 768px) {
  .restablecer .uptask {
    font-size: 10rem;
  }
}
.restablecer input[type=submit] {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .restablecer input[type=submit] {
    width: auto;
  }
}
.restablecer input[type=submit]:hover {
  background-color: #352ca3;
  cursor: pointer;
}
.restablecer .acciones a {
  color: #4338CA;
}